*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff"); }

.main-page {
  height: 100%; }

.page-body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-image: url("../img/backgrounds/pattern.png"), url("../img/backgrounds/boards.png"), url("../img/backgrounds/poligons-header.svg");
  background-color: #1b2d37;
  background-repeat: no-repeat;
  background-size: auto, auto, auto;
  background-position: center, 90% 50%, 90% 50%; }

.page-body .main {
  flex-grow: 1; }

.header {
  max-width: 1431px;
  width: 100%;
  margin: 0 auto;
  padding: 33px 10px 0 10px;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .header {
      padding: 20px 25px; } }
  @media (max-width: 767px) {
    .header {
      padding: 17px; } }

.header__logo-mobile {
  display: none; }
  @media (max-width: 767px) {
    .header__logo-mobile {
      display: block; } }

@media (max-width: 767px) {
  .header__logo-desktop {
    display: none; } }

.header__contacts {
  display: flex; }

.header__contacts-list {
  margin: 0 40px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 1023px) {
    .header__contacts-list {
      margin: 0; } }

.header__contacts-list li {
  align-self: center;
  padding: 5px;
  min-width: 187px; }
  @media (max-width: 1023px) {
    .header__contacts-list li {
      min-width: 50px; } }

.header__contacts-list a {
  position: relative;
  text-decoration: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  display: flex;
  align-items: center;
  column-gap: 20px; }
  .header__contacts-list a:hover, .header__contacts-list a:focus {
    opacity: 0.5; }
  .header__contacts-list a:active {
    opacity: 1; }

@media (max-width: 1023px) {
  .header__email,
  .header__tel {
    display: none; } }

.header__button-order {
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  color: #ffffff;
  height: 45px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  padding: 0 20px;
  align-self: center; }
  .header__button-order:hover, .header__button-order:focus {
    opacity: 0.5; }
  .header__button-order:active {
    opacity: 1; }
  @media (max-width: 1023px) {
    .header__button-order {
      display: none; } }

.main {
  display: grid; }

.main__background {
  background-image: url("../img/backgrounds/background-about.svg");
  background-repeat: no-repeat;
  background-color: #ffffff; }

.lead {
  max-width: 1431px;
  width: 100%;
  padding: 100px 10px 170px;
  margin: 0 auto;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .lead {
      padding: 100px 91px 170px; } }
  @media (max-width: 767px) {
    .lead {
      padding: 100px 17px 170px; } }

.lead a {
  color: inherit;
  text-decoration: none; }

.lead__title {
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  max-width: 817px; }
  @media (max-width: 1023px) {
    .lead__title {
      font-size: 60px;
      line-height: 70px;
      max-width: 470px; } }
  @media (max-width: 767px) {
    .lead__title {
      font-size: 40px;
      line-height: 47px; } }

.lead__description {
  position: relative;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  padding: 0 0 0 115px;
  max-width: 800px; }
  .lead__description::before {
    position: absolute;
    content: "";
    width: 66px;
    height: 2px;
    background-color: #ffffff;
    left: 0;
    top: 16px; }
    @media (max-width: 1023px) {
      .lead__description::before {
        top: 14px;
        width: 33px; } }
    @media (max-width: 767px) {
      .lead__description::before {
        top: 9px; } }
  @media (max-width: 1023px) {
    .lead__description {
      font-size: 18px;
      line-height: 156.8%;
      max-width: 470px;
      padding: 0 0 0 50px; } }
  @media (max-width: 767px) {
    .lead__description {
      font-size: 16px; } }

.lead__button-consultation {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  background-color: #00b2ff;
  border-radius: 5px;
  margin: 0 0 0 115px;
  padding: 15px 20px; }
  @media (max-width: 1023px) {
    .lead__button-consultation {
      margin: 0 0 0 50px; } }
  @media (max-width: 767px) {
    .lead__button-consultation {
      margin: 0; } }

@media (max-width: 767px) {
  .lead__button-text-desktop {
    display: none; } }

.lead__button-text-mobile {
  display: none; }
  @media (max-width: 767px) {
    .lead__button-text-mobile {
      display: inline-block; } }

.lead__link-scroll {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
  transform: rotate(-90deg) translate(-100px, 0);
  margin: 123px 0 0 0;
  display: inline-block;
  width: 154px;
  transform-origin: 0 0; }
  @media (max-width: 1023px) {
    .lead__link-scroll {
      display: none; } }

.features {
  padding: 69px calc(50% - 711px) 110px; }

.features__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px; }

.features__list li {
  color: #00b2ff;
  position: relative;
  padding: 40px 45px;
  border: 1px solid #ebebeb;
  background-color: #ffffff; }
  .features__list li::before {
    position: absolute;
    content: "";
    width: 3px;
    height: calc(100% + 2px);
    background-color: #00b2ff;
    left: -1px;
    top: -1px; }
  .features__list li:hover {
    background-color: #00b2ff;
    color: #ffffff;
    border-color: #00b2ff; }
  .features__list li:hover .features__card-title,
  .features__list li:hover .features__card-description {
    color: #ffffff; }

.features__card-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff; }
  @media (max-width: 767px) {
    .features__card-title {
      font-size: 20px;
      line-height: 140.62%; } }

.features__card-description {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #666666; }
  @media (max-width: 767px) {
    .features__card-description {
      line-height: 21.5px; } }
